<template lang="html">
  <div class="page">
    <div class="page-item">
      <p class="title">user</p>
      <ul class="remark">
        <li class="tip">用户表，储存用户信息</li>
      </ul>
      <table class="produce" cellspacing="0" cellpadding="0">
        <tr>
          <td class="t-title">字段</td>
          <td class="t-title">类型</td>
          <td class="t-title">注释</td>
        </tr>
        <template v-for="item in model.fields">
          <tr>
            <template v-if="GetCharNum(item.name, '.')===1">
              <td>&nbsp;&nbsp;{{item.name.substring(item.name.lastIndexOf('.')+1), item.name.length}}</td>
            </template>
            <template v-else-if="GetCharNum(item.name, '.')===2">
              <td>&nbsp;&nbsp;&nbsp;&nbsp;{{item.name.substring(item.name.lastIndexOf('.')+1), item.name.length}}</td>
            </template>
            <template v-else-if="GetCharNum(item.name, '.')===3">
              <td>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;{{item.name.substring(item.name.lastIndexOf('.')+1), item.name.length}}</td>
            </template>
            <template v-else>
              <td>{{item.name}}</td>
            </template>
            <td>{{item.type}}</td>
            <td>{{item.detail}}</td>
          </tr>
        </template>
      </table>
      <ul class="remark">
        <li>备注：无</li>
      </ul>
    </div>
  </div>
</template>
<script>
export default {
  data() {
    return {
      models: data.models,
      model: data.models[0]
    }
  },
  methods: {
    changeData() {
      let _this = this
      _this.model = _this.models[_this.$route.params.id]
    },
    GetCharNum(s,f){
      let num = 0
      for (let o of s) {
        if (o === f) {
          num++
        }
      }
      return num
    }
  },
  created() {
    let data = data
  },
  watch: {
    // 如果路由有变化，会再次执行该方法
    "$route": "changeData"
  }
}
</script>
<style lang="scss" scoped>
  .page {
    margin-top: -24px;
    background-color: #f4f5f6;
    color: #020f18;
    padding-bottom: 50px;
    .page-item {
      width: calc(100% - 240px);
      background-color: #ffffff;
      margin: 60px 120px;
      padding-bottom: 40px;
      box-shadow: 3px 0 10px rgba(0,62,103,0.08);
      .tip {
        font-size: 18px;
        color: #3c92ca;
      }
      ul {
        li {
          list-style: none;
        }
      }
      p {
        width: calc(100% - 80px);
        margin: 20px 40px;
      }
      .title {
        height: 70px;
        line-height: 70px;
        font-size: 22px;
        border-bottom: solid 1px #b9c4cf;
        color: #020f18;
      }
      .small-title {
        font-size: 16px;
        font-weight: bold;
        margin-bottom: 20px;
      }
      ul {
        display: block;
        list-style-type: disc;
        -webkit-margin-before: 1em;
        -webkit-margin-after: 1em;
        -webkit-margin-start: 0px;
        -webkit-margin-end: 0px;
        -webkit-padding-start: 40px;
        margin-left: 34px;
        margin-bottom: 15px;
        li {
          line-height: 30px;
          margin-left: -16px;
          code {
            background-color: #f7f7f9;
            color: #d14;
            padding: 2px 4px;
            border: 1px solid #e1e1e8;
            border-radius: 3px;
            font-size: 12px;
            font-family: Monaco,Menlo,Consolas,"Courier New",monospace;
          }
        }
      }
      table {
        width: calc(100% - 80px);
        margin: 20px 40px 0px 40px;
        border:solid #E9EFF3; 
        border-width:1px 0px 0px 1px;
        td {
          width: 33.3%;
          border:solid #E9EFF3; 
          border-width:0px 1px 1px 0px;
          height: 35px;
          line-height: 35px;
          padding-left: 80px;
          font-size: 16px;
        }
        .t-title {
          background-color: #ecf3f8;
          color: #020f18;
          font-size: 18px;
          border-bottom: none;
        }
      }
      .produce {
        td {
          width: auto;
        }
      }
      .remark {
        font-size: 14px;
        color: #cccccc;
        margin-top: 10px;
        li {
          display: list-item;
          list-style-type: disc;
          text-align: -webkit-match-parent;
        }
      }
    }
  }
</style>


