import Main from './main.vue'
import Page2 from './components/page2/Page2.vue'
import Page3 from './components/page3/Page3.vue'
import Page4 from './components/page4/Page4.vue'

export default [
  {
    path: '/',
    component: Main,
    redirect: '/page2',
    children: [
      { path: '/page2', name: '全局错误码', component: Page2 },
      { path: '/page3/:id/:subId', name: '接口示例', component: Page3 },
      { path: '/page4/:id', name: '数据字典', component: Page4 }
    ]
  }
]



