<template lang="html">
  <div class="page">
    <div class="page-item">
      <p class="title">用户注册</p>
      <p class="small-title">简要描述：<span>{{api.description}}</span></p>
      <p class="small-title">请求URL：<span><code>{{api.url}}</code></span></p>
      <p class="small-title">请求方式：<span>{{api.method}}</span></p>
      <template v-if="api.headers.length>0">
        <p class="small-title-table">header参数：</p>
        <table cellspacing="0" cellpadding="0">
          <tr>
            <td class="t-title">参数名</td>
            <td class="t-title">类型</td>
            <td class="t-title">必选</td>
            <td class="t-title">说明</td>
          </tr>
          <template v-for="item in api.headers">
            <tr>
              <td>{{item.name}}</td>
              <td>{{item.type}}</td>
              <td>{{item.required}}</td>
              <td>{{item.detail}}</td>
            </tr>
          </template>
        </table>
      </template>

      <template v-if="api.params.length>0">
        <p class="small-title-table">params参数：</p>
        <table cellspacing="0" cellpadding="0">
          <tr>
            <td class="t-title">参数名</td>
            <td class="t-title">类型</td>
            <td class="t-title">必选</td>
            <td class="t-title">说明</td>
          </tr>
          <template v-for="item in api.params">
            <tr>
              <td>{{item.name}}</td>
              <td>{{item.type}}</td>
              <td>{{item.required}}</td>
              <td>{{item.detail}}</td>
            </tr>
          </template>
        </table>
      </template>

      <template v-if="api.body.length>0">
        <p class="small-title-table">body参数：</p>
        <table cellspacing="0" cellpadding="0">
          <tr>
            <td class="t-title">参数名</td>
            <td class="t-title">类型</td>
            <td class="t-title">必选</td>
            <td class="t-title">说明</td>
          </tr>
          <template v-for="item in api.body">
            <tr>
              <template v-if="GetCharNum(item.name, '.')===1">
                <td>&nbsp;&nbsp;{{item.name.substring(item.name.lastIndexOf('.')+1), item.name.length}}</td>
              </template>
              <template v-else-if="GetCharNum(item.name, '.')===2">
                <td>&nbsp;&nbsp;&nbsp;&nbsp;{{item.name.substring(item.name.lastIndexOf('.')+1), item.name.length}}</td>
              </template>
              <template v-else-if="GetCharNum(item.name, '.')===3">
                <td>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;{{item.name.substring(item.name.lastIndexOf('.')+1), item.name.length}}</td>
              </template>
              <template v-else>
                <td>{{item.name}}</td>
              </template>
              <td>{{item.type}}</td>
              <td>{{item.required}}</td>
              <td>{{item.detail}}</td>
            </tr>
          </template>
        </table>
      </template>

      <template v-if="api.query.length>0">
        <p class="small-title-table">query参数：</p>
        <table cellspacing="0" cellpadding="0">
          <tr>
            <td class="t-title">参数名</td>
            <td class="t-title">类型</td>
            <td class="t-title">必选</td>
            <td class="t-title">说明</td>
          </tr>
          <template v-for="item in api.query">
            <tr>
              <td>{{item.name}}</td>
              <td>{{item.type}}</td>
              <td>{{item.required}}</td>
              <td>{{item.detail}}</td>
            </tr>
          </template>
        </table>
      </template>

      <p class="remark">备注：更多返回错误代码请看首页的错误代码描述</p>
    </div>
  </div>
</template>
<script>
//返回实例（下一版）
// <p class="small-title">返回示例</p>
//     <pre id="result"></pre>
//     <p class="small-title">返回参数说明</p>
//     <table class="produce" cellspacing="0" cellpadding="0">
//       <tr>
//         <td class="t-title">参数名</td>
//         <td class="t-title">类型</td>
//         <td class="t-title">说明</td>
//       </tr>
//       <tr>
//         <td>groupid</td>
//         <td>int</td>
//         <td>用户组id，1：超级管理员；2：普通用户</td>
//       </tr>
//     </table>


export default {
  data() {
    return {
      code: '',
      api: {},
    }
  },
  methods: {
    GetCharNum(s,f){
      let num = 0
      for (let o of s) {
        if (o === f) {
          num++
        }
      }
      return num
    },
    makeCode(json) {
      if (typeof json != 'string') {
        json = JSON.stringify(json, undefined, 2)
      }
      json = json.replace(/&/g, '&').replace(/</g, '<').replace(/>/g, '>')
      return json.replace(/("(\\u[a-zA-Z0-9]{4}|\\[^u]|[^\\"])*"(\s*:)?|\b(true|false|null)\b|-?\d+(?:\.\d*)?(?:[eE][+\-]?\d+)?)/g, function (match) {
        var cls = 'number'
        if (/^"/.test(match)) {
          if (/:$/.test(match)) {
            cls = 'key'
          } else {
            cls = 'string'
          }
        } else if (/true|false/.test(match)) {
          cls = 'boolean'
        } else if (/null/.test(match)) {
          cls = 'null'
        }
        return '<span class="' + cls + '">' + match + '</span>'
      });
    },
    make() {
      let json = {
        "code": 200,
        "status": "OK",
        "data": {
          "uid": "1",
          "username": "12154545",
          "name": "吴系挂",
          "groupid": 2 ,
          "reg_time": "1436864169",
          "last_login_time": "0",
          "User": {
            "username": "鳌拜",
            "userId": "123",
            "img": "http://www.baidu.com"
          }
        }
      }
      this.code = this.makeCode(json)
      document.getElementById('result').innerHTML = this.makeCode(json)
    },
    initPage() {
      let _this = this
      console.info(_this.$route.params)
      this.api = data.apis[_this.$route.params.id].apiList[_this.$route.params.subId]
      // setTimeout(function(){
      //   _this.make()
      // },100)
    }
  },
  async created() {
    this.initPage()
  },
  watch: {
    // 如果路由有变化，会再次执行该方法
    "$route": "initPage"
  }
}
</script>
<style lang="scss" scoped>
  .page {
    margin-top: -24px;
    background-color: #f4f5f6;
    color: #020f18;
    padding-bottom: 50px;
    .page-item {
      width: calc(100% - 240px);
      background-color: #ffffff;
      margin: 60px 120px;
      padding-bottom: 40px;
      box-shadow: 3px 0 10px rgba(0,62,103,0.08);
      #result {
        width: calc(60% - 40px);
        margin-left: 20%;
        padding: 20px;
        border: 1px solid #ccc; 
        background-color: #f0f0f0;
        border-radius: 3px;
        font-size: 12px;
        font-family: Monaco,Menlo,Consolas,"Courier New",monospace;
        color: #800;
        line-height: 20px;
        .string { 
          color: green; 
        }
        .number { 
          color: darkorange; 
        }
        .boolean { 
          color: blue; 
        }
        .null { 
          color: magenta; 
        }
        .key { 
          color: red;
        } 
      }
      p {
        width: calc(100% - 80px);
        margin: 30px 40px;
      }
      .title {
        height: 70px;
        line-height: 70px;
        font-size: 22px;
        border-bottom: solid 1px #b9c4cf;
        color: #020F18;
      }
      .small-title {
        font-size: 18px;
        color: #333333;
        span {
          font-size: 16px;
          color: #62696F;
          margin-left: 24px;
          code {
            height: 26px;
            border: solid 1px #dae0e5;
            background-color: #f4f5f6;
            padding: 2px 4px;
            border-radius: 2px;
          }
        }
      }
      .small-title-table {
        font-size: 22px;
        color: #055B93;
        margin: 16px 40px;
      }
      ul {
        display: block;
        list-style-type: disc;
        -webkit-margin-before: 1em;
        -webkit-margin-after: 1em;
        -webkit-margin-start: 0px;
        -webkit-margin-end: 0px;
        -webkit-padding-start: 40px;
        margin-left: 40px;
        margin-bottom: 15px;
        li {
          line-height: 30px;
          margin-left: -16px;
          code {
            background-color: #f7f7f9;
            color: #d14;
            padding: 2px 4px;
            border: 1px solid #e1e1e8;
            border-radius: 3px;
            font-size: 12px;
            font-family: Monaco,Menlo,Consolas,"Courier New",monospace;
          }
        }
      }
      table {
        width: calc(100% - 80px);
        margin: 20px 40px;
        border:solid #E9EFF3; 
        border-width:1px 0px 0px 1px;
        td {
          width: 25%;
          border:solid #E9EFF3; 
          border-width:0px 1px 1px 0px;
          height: 35px;
          line-height: 35px;
          padding-left: 30px;
          font-size: 16px;
        }
        .t-title {
          background-color: #ecf3f8;
          color: #020f18;
          font-size: 18px;
          border-bottom: none;
        }
      }
      .produce {
        td {
          width: auto;
        }
      }
      .remark {
        font-size: 14px;
        color: #cccccc;
        margin-top: 12px;
      }
    }
  }
</style>



