<template lang="html">
  <div class="page">
    <div class="page-item">
      <p class="title">
        全局错误码
      </p>
      <table cellspacing="0" cellpadding="0">
        <tr>
          <td class="t-title">错误码</td>
          <td class="t-title">错误解释</td>
        </tr>
        <template v-for="(value,key,index) in project.ErrorCode">
          <tr>
            <td>{{key}}</td>
            <td>{{value}}</td>
          </tr>
        </template>
      </table>
    </div>
    <div class="page-item">
      <p class="title">
        成功状态示例
      </p>
      <table cellspacing="0" cellpadding="0">
        <tr>
          <td class="t-title">键</td>
          <td class="t-title">值</td>
        </tr>
        <template v-for="(value,key,index) in project.apiSuccessTemplate">
          <tr>
            <td>{{key}}</td>
            <td>{{value}}</td>
          </tr>
        </template>
      </table>
    </div>
    <div class="page-item">
      <p class="title">
        失败状态示例
      </p>
      <table cellspacing="0" cellpadding="0">
        <tr>
          <td class="t-title">键</td>
          <td class="t-title">值</td>
        </tr>
        <template v-for="(value,key,index) in project.apiErrorTemplate">
          <tr>
            <td>{{key}}</td>
            <td>{{value}}</td>
          </tr>
        </template>
      </table>
    </div>
    <div class="page-item">
      <p class="title">
        条件查询示例
      </p>
      <table cellspacing="0" cellpadding="0">
        <tr>
          <td class="t-title">键</td>
          <td class="t-title">值</td>
        </tr>
        <tr>
          <td>sort</td>
          <td>排序</td>
        </tr>
        <tr>
          <td>limit</td>
          <td>限制返回数量（分页）</td>
        </tr>
        <tr>
          <td>skip</td>
          <td>跳过返回数量（分页）</td>
        </tr>
      </table>
    </div>
  </div>
</template>
<script>
export default {
  data() {
    return {
      project: {}
    }
  },
  methods: {
    
  },
  async created() {
    let _this = this
    setTimeout(function(){
      _this.project = data.project
    },300)
  }
}
</script>
<style lang="scss" scoped>
  .page {
    margin-top: -24px;
    background-color: #f4f5f6;
    color: #020f18;
    padding-bottom: 50px;
    .page-item {
      width: calc(100% - 240px);
      background-color: #ffffff;
      margin: 60px 120px;
      padding-bottom: 40px;
      box-shadow: 3px 0 10px rgba(0,62,103,0.08);
      p {
        width: calc(100% - 80px);
        margin: 20px 40px;
      }
      .title {
        height: 70px;
        line-height: 70px;
        font-size: 22px;
        color: #020f18;
        border-bottom: solid 1px #b9c4cf;
        font-family: "MicrosoftYaHei";
      }
      table {
        width: calc(100% - 80px);
        margin: 20px 40px;
        border:solid #E9EFF3; 
        border-width:1px 0px 0px 1px;
        td {
          width: 50%;
          border:solid #E9EFF3; 
          border-width:0px 1px 1px 0px;
          height: 35px;
          line-height: 35px;
          padding-left: 122px;
        }
        .t-title {
          background-color: #ecf3f8;
          color: #020f18;
          border-bottom: none;
        }
      }
    }
  }
</style>


