<template lang="html">
  <el-menu default-active="1-2" id="nav-menu" theme="dark" @select="handleSelect">
    <div class="logo">
      <router-link to="/">
        <img src="../../assets/images/logo.png" />
      </router-link>
    </div>
    <el-menu-item index="1-2"><router-link to="/page2" class="nav-link icon-first-menu">全局错误码</router-link></el-menu-item>
    <el-submenu index="1-3">
      <template slot="title"><span class="interface">接口列表</span></template>
      <template v-for="(item, index) in apis">
        <el-submenu :index="1-3-index">
          <template slot="title">{{item.related}}</template>
          <template v-for="(it, i) in item.apiList">
            <el-menu-item :index="1-3-index-i"><router-link :to="{ path:`/page3/${index}/${i}` }" class="nav-link">{{it.name}}</router-link></el-menu-item>
          </template>
        </el-submenu>
      </template>
    </el-submenu>
    <el-submenu index="2">
      <template slot="title"><span class="dictionary">数据字典</span></template>
      <template v-for="(it, i) in models">
        <el-menu-item :index="2-i"><router-link :to="{ path:`/page4/${i}` }" class="nav-link">{{it.name}}</router-link></el-menu-item>
      </template>
    </el-submenu>
  </el-menu>
</template>
<script>
// import data from '../../demo.json'
export default {
  data() {
    return {
      apis: [],
      models: []
    }
  },
  methods: {
    handleSelect(key, keyPath) {
      console.log(key, keyPath);
    }
  },
  async created() {
    let _this = this
    let timer = setTimeout(function(){
      _this.apis = data.apis
      _this.models = data.models
    },500)
  }
}
</script>


